as, folletos ilustrativos, plegables, etc., en los que los textos pasan por columnas de página en página Se acomoda en columnas verticales con el objetivo de distribuir mejor la información para una mejor lectura. Las columnas dependen unas de otras y pueden ser muy flexibles. Su ancho depende del tamaño de la página y pueden diseñarse entre dos y 6 columnas por páginas. Dos columnas para tamaños pequeños y 6 columnas para periódicos de tamaño tradicional (aproximadamente 58 cm de alto por 38 cm de ancho). Se pueden reservar filas para titulares y entradillas, zonas espaciales para imágenes y marcadores para textos secundarios. ")])]),_c('div',{staticClass:"col-lg-5"},[_c('img',{staticClass:"img-a img-t my-3",attrs:{"src":require("@/assets/curso/temas/img113.svg"),"alt":""}})])])])],1)]),_c('Separador'),_vm._m(9),_c('div',{staticClass:"bg9"},[_vm._m(10),_c('Separador'),_vm._m(11),_vm._m(12),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("La imagen visual es un factor vital para las empresas porque los recursos visuales y de diseño son los que representan el mensaje que la marca quiere comunicar a su público objetivo. Dentro de los elementos que conforman la parte visual, se recalcan:")]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-12 bg9-1 up"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-11"},[_vm._m(13),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Es el logo, la firma que debe llevar toda pieza gráfica. Es la manera en que una marca se representa gráficamente, por lo que ese rasgo, es el comienzo para diferenciarse de otras marcas. El distintivo visual tiene diferentes tipos de poder ser creado:")]),_c('div',{staticClass:"row mb-md-5",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"col-lg-3 col-md-6 mb-3"},[_c('div',{staticClass:"tarjeta tarjeta-foto-hover hw b-rad indicador__container",on:{"mouseove