vm._v("La identidad corporativa debe ser coherente al grupo de valores que conforman la visión empresarial. Los principios, la filosofía, las acciones de endomarketing, la actitud que acoge en distintas situaciones y la imagen que la marca desea establecer, hacen parte de su identidad. Por eso, va mucho más lejos de la simple apariencia visual y se relaciona con cuestiones culturales y empresariales de importancia para la marca. Por eso, este tema es relevante para cualquier tipo de marca sin importar tamaños ni segmentos porque es vital en la relación que tendrá no solo con los clientes, sino también con distribuidores, proveedores y aliados estratégicos. ")])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{staticClass:"img-a img-t my-3",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img114.png"),"alt":""}})])]),_c('div',{staticClass:"bg6 p-3",attrs:{"data-aos":"flip-down"}},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-down"}},[_vm._v("Los elementos que conforman la identidad de marca son una mixtura de dos aspectos, los esenciales y los visuales. Los esenciales porque hacen parte en la interacción directa con los clientes como lo son, entre otros, el servicio y la atención. Los visuales, todo lo relacionado con las piezas gráficas (distintivo visual, papelería empresarial, souvenires, uniformes, transporte empresarial, arquitectura, publicidad exterior, anuncios impresos y digitales, etc.), y donde se trabaja fuertemente la imagen visual a través del uso correcto de colores, fuentes tipográficas, lenguaje, elementos gráficos, etc. CITATION Joa93 \\l 2058 (Costa, Identidad corporativa, 1993). ")])])]),_c('div',{staticClass:"col-lg-3"},[_c('img',{staticClass:"img-a img-t my-3",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img115.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-contenido",attrs:{"id":"t_4_4","data-aos":"