on demasiadas y pueden connotar desorden. ")])]),_c('div',{staticClass:"col-lg-5"},[_c('img',{staticClass:"img-a img-t my-3",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img124.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-5"},[_c('img',{staticClass:"img-a img-t my-3",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img125.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"row justify-content-start align-items-center",attrs:{"data-aos":"flip-down"}},[_c('div',{staticClass:"col-lg-4"},[_c('h4',{staticClass:"title text-center p-2"},[_vm._v("Público objetivo ")])])]),_c('div',{staticClass:"bloque-texto-b color-secundario px-4",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"bloque-texto-b__texto"},[_c('i',{staticClass:"fas fa-quote-left"}),_c('p',{staticClass:"mb-0"},[_vm._v("La identidad corporativa y la imagen visual de una marca deben tener en cuenta la segmentación, es decir, el público al que van dirigidos sus productos y servicios, también conocidos como “"),_c('em',[_vm._v("buyer")]),_vm._v(" personas”. No es lo mismo dirigirse a un público joven de 25 años en promedio que a un público más adulto de aproximadamente 60 años de edad. Se debe adecuar la identidad y la imagen al público objetivo porque de esta manera se trabaja mejor la coherencia y el contexto del mensaje, también se construye con mayor fortaleza la reputación de la marca. ")]),_c('p',[_c('b',[_vm._v("(Tamayo, 2014).")])]),_c('i',{staticClass:"fas fa-quote-right"})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-contenido",attrs:{"id":"t_4_5","data-aos":"zoom-in-left"}},[_c('h2',[_vm._v("4.5 Diagramación y composición ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-9"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Cumpa (2002) explica que la diagramación y la composición son temas relacionados, esencialmente, a los fundamentos que tiene el diseño gráfico como profesión, y en donde se analizan y se trabajan la composición, el diseño, la tipografía, las imágenes, los elementos gráficos y el color. Mediante la diagramación un diseñador se va a encargar de ordenar de la manera más adecuada todo el espacio de las diferentes piezas gráficas, de tal forma que el producto final sea atractivo para los ojos del consumidor. ")]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-down"}},[_vm._v("Desde piezas impresas, hasta piezas multimediales, digitales y audiovisuales van a tener que desarrollar un trabajo de diagramación y composición para que todos los elementos que hagan parte de su diseño, se vean bien y, además, se entregue la información que la marca quiere enviar a sus clientes. La diagramación también incluye todo el proceso de producción, como por ejemplo la pre prensa (preparar los archivos para la impresión), de las diferentes piezas gráficas, antes de ser enviadas a los medios litográficos, audiovisuales o digitales. Incluye además la post prensa, momento en el cual, luego de producidas las piezas gráficas, comienza un trabajo de acabado. Por ejemplo, en los impresos, el recorte de un troquel, el laminado o esmaltado de algunas áreas de la pieza gráfica, el doblez (en el caso de los plegables), etc.")])]),_c('div',{staticClass:"col-lg-4"},[_c('img',{staticClass:"img-a img-t my-3",attrs:{"data-aos":"zoom-in","src":require("@/assets/curso/temas/img127.png"),"alt":""}})])])]),_c('div',{staticClass:"col-lg-3"},[_c('img',{staticClass:"img-a img-t my-3",attrs:{"data-aos":"zoom-in","src":require("@/a